<template>
    <div class="Flight_bookingsView">
        <loading :active.sync="isLoading" :can-cancel="false" :is-full-page="fullPage"></loading>

        <!-- context-menu -->
        <div id="context-menu" class="context-menu" :style="menuStyle">
            <ul>
                <li class="dropdown-item" @click="delete_flight_booking(this_row.id)">
                    <i class="fa fa-trash"></i>
                    {{ $t("Delete") }}
                </li>
                <li class="ltr dropdown-item" @click="open_edit_modal">
                    <i class="fa fa-pen-to-square"></i>
                    {{ $t("Edit") }}
                </li>
                <li class="dropdown-item" @click="PrintDiv('flight_booking_data')">
                    <i class="fa fa-print"></i>
                    {{ $t("Print") }}
                </li>
            </ul>
        </div>


        <!-- Table Card -->
        <div class="col-xl-12 center">
            <div class="card shadow mb-4">
                <!-- Card Header - Dropdown -->
                <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
                    <h6 class="m-0 font-weight-bold text-primary">{{ $t("Flight Bookings Table") }} </h6>
                    <div class="btn-group" role="group">
                        <button type="button" title="Print" class="btn" @click="PrintDiv('table')">
                            <i class="fa fa-print on-hover"></i>
                        </button>

                        <button type="button" title="Add" class="btn" @click="open_add_modal">
                            <i class="fa fa-plus on-hover"></i>
                        </button>
                    </div>
                </div>
                <!-- Card Body -->
                <div id="table" class="card-body">
                    <div class="card-header cnter" style="display: none;" id="head_txt"></div>
                    <form class="site-form-table">
                        <div id="main_table" class="table-responsive">
                            <table class="table table-hover">
                                <thead class="sticky_header">
                                    <tr>
                                        <th scope="col">#</th>
                                        <th scope="col">{{ $t("Depart Date") }}</th>
                                        <th scope="col">{{ $t("Return Date") }}</th>
                                        <th scope="col">{{ $t("Type") }}</th>
                                        <th scope="col">{{ $t("Persons") }}</th>
                                        <th scope="col">{{ $t("Infants") }}</th>
                                        <th scope="col">{{ $t("Flight Codes") }}</th>
                                        <th scope="col">{{ $t("Notes") }}</th>
                                        <th scope="col">{{ $t("User") }}</th>
                                        <th scope="col" class="no_print">{{ $t("Actions") }}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr @contextmenu="showContextMenu" v-for="f in this.flight_booking_rows" :key="f.id"
                                        @click="row_click(f.id)" @click.right="row_click(f.id)" @dblclick="open_edit_modal"
                                        role="button" :class="{ 'selected': f.id === active_index }">
                                        <th scope="row" id="id">{{ f.id }}</th>
                                        <td>{{ formatDate(f.flight1_date) }}</td>

                                        <td v-if="f.type == 'Roundtrip'">{{ formatDate(f.flight2_date) }}</td>

                                        <td>{{ $t(flight_booking.type) }}</td>
                                        <td>{{ $t(f.persons) }}</td>
                                        <td>{{ $t(f.infants) }}</td>
                                        <td>{{ f.flight1_code }} {{ f.flight2_code }} {{ f.flight3_code }} {{ f.flight4_code }}
                                            {{ f.flight5_code }}</td>
                                        <td>{{ $t(f.notes) }}</td>
                                        <td>{{ $t(f.user) }}</td>

                                        <td class="no_print">

                                            <div class="btn-group" role="group">
                                                <button id="btnGroupVerticalDrop1" type="button" class="btn dropdown-toggle"
                                                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                                                    <i class="fa fa-gear on-hover"></i>
                                                </button>
                                                <div class="dropdown-menu" aria-labelledby="btnGroupVerticalDrop1"
                                                    x-placement="bottom-start"
                                                    style="position: absolute; will-change: transform; top: 0px; left: 0px; transform: translate3d(0px, 37px, 0px);">
                                                    <a class="dropdown-item"
                                                        @click="delete_flight_booking(flight_booking.id)">
                                                        <i class="fa fa-trash"></i>
                                                        {{ $t("Delete") }}
                                                    </a>
                                                    <a class="dropdown-item" @click="open_edit_modal">
                                                        <i class="fa fa-pen-to-square"></i>
                                                        {{ $t("Edit") }}
                                                    </a>
                                                    <a class="dropdown-item" @click="PrintDiv('flight_booking_data')">
                                                        <i class="fa fa-print"></i>
                                                        {{ $t("Print") }}
                                                    </a>
                                                </div>
                                            </div>

                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- to get id of selected record -->
                        <input hidden type="text" v-model="active_index" id="row_id" />
                    </form>
                </div>
            </div>
        </div>

        <!-- modal -->
        <div class="modal fade" id="addModal" data-backdrop="static" data-keyboard="false" tabindex="-1"
            aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div class="modal-dialog modal-xl">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 v-if="!this.edit_mode" class="modal-title" id="modal_label">{{ $t("Add Flight Booking") }}</h5>
                        <h5 v-if="this.edit_mode" class="modal-title" id="modal_label">{{ $t("Edit Flight Booking") }}</h5>
                        <button type="button" class="close on-hover" @click="this.closeModal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">

                        <form>

                            <div class="row g-3 form-group">

                                <div class="form-group col-sm-4">
                                    <label for="type">{{ $t("Type") }}</label>
                                    <v-select id="type" v-model="this_row.type" :options="types"
                                        :class="{ 'is-invalid': !this_row.type && validate, 'is-valid': this_row.type && validate }" />

                                    <div v-if="!this_row.type && validate" class="invalid-feedback hidden">
                                        {{ $t("Please Select type") }}
                                    </div>
                                </div>

                                <div class="form-group col-sm-4">
                                    <label for="from_city">{{ $t("From") }}</label>
                                    <v-select id="from_city" v-model="from_city" :options="citys"
                                        :class="{ 'is-invalid': !from_city && validate, 'is-valid': from_city && validate }" />

                                    <div v-if="!from_city && validate" class="invalid-feedback hidden">
                                        {{ $t("Please Select From City") }}
                                    </div>
                                </div>

                                <div class="form-group col-sm-4">
                                    <label for="to_city">{{ $t("To") }}</label>
                                    <v-select id="to_city" v-model="to_city" :options="citys"
                                        :class="{ 'is-invalid': !to_city && validate, 'is-valid': to_city && validate }" />

                                    <div v-if="!to_city && validate" class="invalid-feedback hidden">
                                        {{ $t("Please Select To City") }}
                                    </div>
                                </div>


                                <div class="form-group"
                                    :class="{ 'col-sm-6': this_row.type == 'Roundtrip', 'col-sm-12': this_row.type != 'Roundtrip' }">
                                    <label for="depart_date">{{ $t("Depart Date") }}</label>
                                    <input id="depart_date" v-model="this_row.depart_date" type="date"
                                        :class="{ 'is-invalid': !this.this_row.depart_date && this.validate, 'is-valid': this.this_row.depart_date && this.validate }"
                                        class="form-control">
                                    <div v-if="!this.this_row.depart_date && this.validate" class="invalid-feedback hidden">
                                        {{ $t("Please Select The Depart Date") }}
                                    </div>
                                </div>

                                <div v-show="this_row.type == 'Roundtrip'" class="form-group col-sm-6">
                                    <label for="return_date">{{ $t("Return Date") }}</label>
                                    <input id="return_date" v-model="this_row.return_date" type="date"
                                        :class="{ 'is-invalid': !this.this_row.return_date && this.validate, 'is-valid': this.this_row.return_date && this.validate }"
                                        class="form-control">
                                    <div v-if="!this.this_row.return_date && this.validate" class="invalid-feedback hidden">
                                        {{ $t("Please Select The Depart Date") }}
                                    </div>
                                </div>

                            </div>

                            <div class="row g-3 form-group">

                                <div class="form-group col-sm-6">
                                    <label for="persons"> {{ $t("Persons") }}</label>
                                    <input id="persons" v-model="this_row.persons"
                                        :class="{ 'is-invalid': !this.this_row.persons && this.validate, 'is-valid': this.this_row.persons && this.validate }"
                                        type="number" class="form-control">
                                    <div v-if="!this.this_row.persons && this.validate" class="invalid-feedback hidden">
                                        {{ $t("Please Enter The Number Of Persons") }}
                                    </div>
                                </div>

                                <div class="form-group col-sm-6">
                                    <label for="infants"> {{ $t("Infants") }}</label>
                                    <input id="infants" v-model="this_row.infants"
                                        :class="{ 'is-invalid': !this.this_row.infants && this.validate, 'is-valid': this.this_row.infants && this.validate }"
                                        type="number" class="form-control">
                                    <div v-if="!this.this_row.infants && this.validate" class="invalid-feedback hidden">
                                        {{ $t("Please Enter The Number Of infants") }}
                                    </div>
                                </div>

                            </div>

                            <div class="row g-3 form-group">
                                <button type="button" @click="get_go_flight(); get_back_flight();"
                                    class="btn btn-light btn-lg btn-block"><i class="fa-solid fa-magnifying-glass"></i>
                                </button>
                            </div>

                            <div class="card form-group">
                                <div v-for="f in go_flights" :key="f.id" class="form-check">
                                    <input class="form-check-input" type="radio" name="exampleRadios" id="exampleRadios1"
                                        value="option1" checked>
                                    <label class="form-check-label" for="exampleRadios1">
                                        {{ f }}
                                    </label>
                                </div>

                                <div class="card-header"> {{ $t("Go Flight") }} </div>
                                <div class="card-body">
                                    <div class="row g-3 form-group">

                                        <div class="col">
                                            <label for="go_flight_code">{{ $t("Flight") }} </label>
                                            <v-select id="go_flight_code" v-model="this_row.go_flight_code"
                                                :options="go_flights"
                                                :class="{ 'is-invalid': !this_row.go_flight_code && validate, 'is-valid': this_row.go_flight_code && validate }" />
                                            <div v-if="!this_row.go_flight_code && validate"
                                                class="invalid-feedback hidden">
                                                {{ $t("Please Select flight") }}
                                            </div>
                                        </div>

                                        <div class="col">
                                            <label for="from_airport">{{ $t("From Airport") }}</label>
                                            <input id="from_airport" v-model="go_flight_data.from_airport"
                                                class="form-control" readonly />
                                        </div>

                                        <div class="col">
                                            <label for="to_airport">{{ $t("To Airport") }}</label>
                                            <input id="to_airport" v-model="go_flight_data.to_airport" class="form-control"
                                                readonly />
                                        </div>

                                    </div>

                                    <p v-show="go_flight_data.available_seats && this_row.go_flight_code">
                                        Available Seats: <span class="badge badge-success">{{ go_flight_data.available_seats
                                        }}</span>
                                    </p>

                                </div>
                            </div>

                            <div v-show="this_row.type == 'Roundtrip'" class="card form-group">
                                <div class="card-header"> {{ $t("Back Flight") }} </div>
                                <div class="card-body">
                                    <div class="row g-3 form-group">

                                        <div class="col">
                                            <label for="back_flight_code">{{ $t("Flight") }}</label>
                                            <v-select id="back_flight_code" v-model="this_row.back_flight_code"
                                                :options="back_flights"
                                                :class="{ 'is-invalid': !this_row.back_flight_code && validate, 'is-valid': this_row.back_flight_code && validate }" />
                                            <div v-if="!this_row.back_flight_code && validate"
                                                class="invalid-feedback hidden">
                                                {{ $t("Please Select flight") }}
                                            </div>
                                        </div>

                                        <div class="col">
                                            <label for="from_airport">{{ $t("From Airport") }}</label>
                                            <input id="from_airport" v-model="back_flight_data.from_airport"
                                                class="form-control" readonly />
                                        </div>

                                        <div class="col">
                                            <label for="to_airport">{{ $t("To Airport") }}</label>
                                            <input id="to_airport" v-model="back_flight_data.to_airport"
                                                class="form-control" readonly />
                                        </div>

                                    </div>
                                    <p v-show="back_flight_data.available_seats && this_row.back_flight_code">
                                        Available Seats: <span class="badge badge-success">{{
                                            back_flight_data.available_seats
                                        }}</span>
                                    </p>
                                </div>
                            </div>

                            <div class="form-group ">
                                <label for="cutomer_notes"> {{ $t("Notes") }}</label>
                                <textarea class="form-control" v-model="this_row.notes" id="cutomer_notes"
                                    rows="1"></textarea>
                            </div>

                            <div class="form-check">
                                <input v-model="print" class="form-check-input" type="checkbox" value=""
                                    id="flexCheckChecked" checked>
                                <label class="form-check-label" for="flexCheckChecked">
                                    Print
                                </label>
                            </div>
                        </form>

                    </div>
                    <div class="modal-footer">

                        <button type="button" id="closeModal" title="close" class="btn btn-secondary on-hover-sm"
                            @click="this.closeModal">
                            <i class="fa fa-xmark"></i>
                        </button>

                        <button v-if="!edit_mode" type="button" title="save" @click="save_flight_booking()"
                            class="btn btn-primary on-hover-sm">
                            <i class="fa fa-floppy-disk"></i></button>

                        <button v-if="edit_mode" type="button" title="delete" @click="delete_flight_booking(active_index)"
                            class="btn btn-danger on-hover-sm"> <i class="fa fa-trash"></i> </button>

                        <button v-if="edit_mode" type="button" title="save" @click="update_flight_booking(active_index)"
                            class="btn btn-primary on-hover-sm"> <i class="fa fa-floppy-disk"></i> </button>

                    </div>
                </div>
            </div>
        </div>

        <!-- to get search value from navbar -->
        <input :value="this.$parent.$refs.NavBar.search" v-bind:on-change="search" hidden>

    </div>
</template>

<script>
/* eslint-disable */

// import block
import axios from 'axios';
import { my_api, domain_url } from "../axios-api";
import NavBar from '../components/parts/NavBar.vue';
import VuePersianDatetimePicker from 'vue-persian-datetime-picker';
import vSelect from 'vue-select';
import 'vue-select/dist/vue-select.css';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

// end of import block

export default {
    name: 'Flight_bookingsView',

    /////////////////////
    components: {
        NavBar,
        datePicker: VuePersianDatetimePicker,
        vSelect,
        Loading,
    },

    ////////////////////

    data() {
        return {
            isLoading: false,
            fullPage: true,
            is_range: true,
            max_id: '',
            user_roles: "",
            print: false, //to print after save
            validate: false, //for check forms
            active_index: null,//current id
            edit_mode: false,//edit form open
            add_mode: false,//add form open
            flight_booking_rows: [],//all rows
            go_flights: [],
            back_flights: [],
            go_flight_data: [],
            back_flight_data: [],
            //////
            this_row: {
                type: "",
                depart_date: "",
                return_date: "",
                persons: "",
                infants: "",
                go_flight_code: "",
                back_flight_code: "",
                notes: "",
                user: "",
            },
            types: ['Roundtrip', 'One-way', 'Multi-city'],
            citys: [],
            from_city: '',
            to_city: '',
            //////

            //////contex menu
            isContextMenuActive: false,
            menuStyle: {
                display: 'none',
                top: 0,
                left: 0
            }///////////

        }
    },

    ////////////////////
    watch: {

        // 'this_row.go_flight_code': async function (newValue) {
        //     if (this.edit_mode || this.add_mode) {
        //         this.this_row.room_id = '';

        //         await this.get_rooms();
        //         this.this_row.room_id = this.rooms[0];

        //     }
        // },

        // 'this_row.room_id': function (newValue) {
        //     if (this.edit_mode || this.add_mode) {
        //         this.get_room_info(newValue);
        //         this.get_booked_dates();
        //     }
        // },

        'this_row.go_flight_code': function (newValue) {
            if (newValue) {
                this.get_go_flight_data(newValue)
            }
        },

        'this_row.back_flight_code': function (newValue) {
            if (newValue) {
                this.get_back_flight_data(newValue)
            }
        },

        //call get_go_flight() when any value changed
        // "this_row.depart_date": "get_go_flight",
        // "this_row.return_date": "get_back_flight",

        // from_city: function () {
        //     this.get_go_flight()
        //     this.get_back_flight()
        // },
        // to_city: function () {
        //     this.get_go_flight()
        //     this.get_back_flight()
        // },
        // persons: function () {
        //     this.get_go_flight()
        //     this.get_back_flight()
        // },


    },

    ////////////////////

    async mounted() {
        this.isLoading = true;
        this.this_row.user = localStorage.getItem('user_name'); //to get logged in user name

        //get roles
        await new Promise(resolve => setTimeout(resolve, 500)); // wait
        await this.get_roles();
        if (this.user_roles[this.$route.path.substring(1)] == 0) {
            this.$router.back()
        }

        await this.get_flight_booking_rows();
        await this.get_citys();
        await this.update_seats();

        this.isLoading = false;
        //to remove modal background on auto vue js reload
        const elements = document.getElementsByClassName("modal-backdrop fade show");
        while (elements.length > 0) {
            elements[0].parentNode.removeChild(elements[0]);
        }////


    },

    ////////////////////

    computed: {
        search() {
            let data = this.$parent.$refs.NavBar.search
            if (data && data.trim()) { // data.trim() to check data not spaces only
                return axios({
                    method: "get",
                    url: domain_url + "/backend/flight_bookings/?search=" + data,
                }).then((response) => (this.flight_booking_rows = response.data));
            } else {
                this.get_flight_booking_rows();
            }
        },

    },

    ////////////////////

    methods: {

        formatDate(date) {
            const formattedDate = new Date(date);
            const day = String(formattedDate.getDate()).padStart(2, '0');
            const month = String(formattedDate.getMonth() + 1).padStart(2, '0');
            const year = formattedDate.getFullYear();
            const hours = String(formattedDate.getHours()).padStart(2, '0');
            const minutes = String(formattedDate.getMinutes()).padStart(2, '0');

            if (year && month && day) {
                return `${year}-${month}-${day}`;
            } else {
                return '-'
            }
        },

        async get_roles() {
            return my_api.get('backend/get_roles/?user_name=' + this.this_row.user, { auth: { username: "admin", password: "123", } })
                .then((response) => (this.user_roles = response.data))
                .catch(err => { });
        },

        async update_seats() {
            return my_api.post('backend/update_seats/', { auth: { username: "admin", password: "123", } })
                .catch(err => { });
        },

        // page load **********************************

        get_flight_booking_rows() {
            // we using return first of the function for 'await' 
            return my_api.get('/backend/flight_bookings/')
                .then((response) => (this.flight_booking_rows = response.data))
                .catch(err => { alert(err) });
        },

        async get_flight_book(id) {
            try {
                const response = await axios.get(`${domain_url}/backend/flight_bookings/?id=${id}`);
                this.this_row = response.data[0];
            } catch (error) {
                console.error("Error fetching booking:", error);
                // Handle the error gracefully (e.g., display an error message to the user)
            }
        },

        // end page load *******************************


        // insert form *******************************

        get_go_flight_data(code) {
            this.go_flight_data = [];
            return axios({
                method: "get",
                url: domain_url + "/backend/get_flight_data/", params: { flight_code: code },
            }).then((response) => (this.go_flight_data = response.data.data[0]));
        },

        get_back_flight_data(code) {
            this.back_flight_data = [];
            return axios({
                method: "get",
                url: domain_url + "/backend/get_flight_data/", params: { flight_code: code },
            }).then((response) => (this.back_flight_data = response.data.data[0]));
        },

        get_go_flight() {
            if (this.this_row.depart_date) {
                this.go_flights = []; this.this_row.go_flight_code = '';
                return axios({
                    method: "get",
                    url: domain_url + "/backend/get_flight/", params: { date: this.this_row.depart_date, from_city: this.from_city, to_city: this.to_city, persons: this.this_row.persons },
                }).then((response) => (this.go_flights = response.data.response_data.map(item => item.code)));
            }
        },

        get_back_flight() {
            if (this.this_row.return_date) {
                this.back_flights = []; this.this_row.back_flight_code = '';
                return axios({
                    method: "get",
                    url: domain_url + "/backend/get_flight/", params: { date: this.this_row.return_date, from_city: this.to_city, to_city: this.from_city, persons: this.this_row.persons },
                }).then((response) => (this.back_flights = response.data.response_data.map(item => item.code)));
            }
        },

        PrintDiv(id) {
            var divToPrint = document.getElementById(id);
            var popupWin = window.open('', '_blank', 'width=100000,height=10000');
            document.getElementById('head_txt').style.display = "block";
            document.getElementById('head_txt').innerHTML = this.$t("Flight Bookings");
            popupWin.document.write('<link href="static/css/sb-admin-2.min.css" rel="stylesheet">');
            popupWin.document.write('<link href="static/css/reports.css" rel="stylesheet">');
            popupWin.document.write('<style>body{background-color:white !important;}</style>');
            popupWin.document.write('<iframe src="static/parts/report_head.html" width="100%" height="200px" frameBorder="0"></iframe>');
            popupWin.document.write('<html><body onload="window.print()"> ' + divToPrint.innerHTML + '</html>');
            document.getElementById('head_txt').style.display = "none";
            popupWin.document.close();
        },

        // end insert form *******************************


        // for modals *******************************

        open_add_modal() {
            this.edit_mode = false;
            this.add_mode = true;
            this.clear_form();
            this.this_row.book_date = new Date().toISOString().slice(0, 16);

            $('#addModal').modal('toggle');
        },

        async open_edit_modal() {
            if (this.user_roles.hotels) {

                this.edit_mode = true;
                this.add_mode = false;

                // remove this booked dates from disable_dates to enable edit booked dates
                this.this_row.all_dates = [];

                if (this.this_row.dates.length < 2) {
                    this.this_row.dates.push(this.this_row.dates[0]);
                } else {
                    await this.get_booked_dates(); // to disable other booked dates 
                }
                const minDateParts = this.this_row.dates[0].split('/');
                const maxDateParts = this.this_row.dates[1].split('/');
                const startDate = new Date(minDateParts[2], minDateParts[1] - 1, minDateParts[0]);
                const endDate = new Date(maxDateParts[2], maxDateParts[1] - 1, maxDateParts[0]);

                const currentDate = new Date(startDate);
                while (currentDate <= endDate) {
                    this.this_row.all_dates.push(currentDate.toLocaleDateString("en-GB"));
                    currentDate.setDate(currentDate.getDate() + 1);
                }
                this.disable_dates = this.disable_dates.filter((el) => !this.this_row.all_dates.includes(el));
                //////////////////////////////////////////////
                await this.get_rooms();
                $('#addModal').modal('toggle');
            }
        },

        clear_form() {
            this.this_row.type = "Roundtrip";
            this.this_row.depart_date = '';
            this.this_row.return_date = '';
            this.this_row.persons = 1;
            this.this_row.infants = 0;
            this.this_row.go_flight_code = '';
            this.this_row.back_flight_code = '';
            this.this_row.notes = '';
            this.validate = false;
        },

        async closeModal() {
            await this.update_seats();
            this.add_mode = false; this.edit_mode = false;
            $('#addModal').modal('hide');
            this.clear_form();
        },

        // end for modals ***************************

        async save_flight_booking() {
            this.isLoading = true;
            try {
                if (this.check_form()) {
                    if (this.print) {
                        this.PrintDiv('flight_booking_data');
                    }

                    if (!this.this_row.infants) { this.this_row.infants = 0; }
                    this.this_row.depart_date = this.formatDate(this.this_row.depart_date)
                    var response = await fetch(domain_url + "/backend/flight_bookings/", {
                        method: "post",
                        headers: { "Content-Type": "application/json", },
                        body: JSON.stringify(this.this_row),
                    });

                    swal(this.$t("Added!"), { buttons: false, icon: "success", timer: 1500, });

                    await this.get_max_id();

                    //save log
                    axios.post(domain_url + '/backend/logs/', { user_name: this.$parent.user_name, log: 'add flight booking :' + this.max_id, time: new Date() })

                    this.get_flight_booking_rows();

                    this.closeModal();
                }
            } catch (error) { console.error(); }
            this.isLoading = false;
        },

        async delete_flight_booking(id) {

            this.isLoading = true;
            try {
                const willDelete = await swal({ title: this.$t("Are you sure to delete?"), text: "", icon: "warning", buttons: true, dangerMode: true, });

                if (willDelete) {
                    await my_api.delete(`/backend/flight_bookings/${id}/`);
                    swal(this.$t("Deleted!"), { buttons: false, icon: "success", timer: 2000, });

                    //save log
                    axios.post(domain_url + '/backend/logs/', { user_name: this.$parent.user_name, log: 'delete flight booking :' + id, time: new Date() })


                    this.get_flight_booking_rows();
                    this.closeModal();
                }
            } catch (error) {
                console.error("Error deleting booking:", error);
            }
            this.isLoading = false;

        },

        async update_flight_booking(id) {
            this.isLoading = true;
            try {
                if (this.check_form()) {
                    if (this.print) {
                        await this.PrintDiv('flight_booking_data');
                    }

                    var response = await fetch(domain_url + "/backend/flight_bookings/" + id + "/", {
                        method: "PUT",
                        headers: { "Content-Type": "application/json", },
                        body: JSON.stringify(this.this_row),
                    }
                    );
                    swal(this.$t("Updated!"), { buttons: false, icon: "success", timer: 2000, });
                    //save log
                    axios.post(domain_url + '/backend/logs/', { user_name: this.$parent.user_name, log: 'update flight booking :' + id, time: new Date() })

                    //this.max_id = this.this_row.id
                    this.get_flight_booking_rows();
                    this.closeModal();
                    this.edit_mode = false;
                }
            } catch (error) {
                console.error();
            }
            this.isLoading = false;
        },

        async row_click(index) {
            await this.get_flight_book(index);//get this row data
            this.active_index = index; //to change row color
        },

        check_form() {
            this.validate = true; //to change inputs color 'red/green'

            if (
                this.this_row.type &&
                this.this_row.depart_date &&
                this.this_row.persons > 0 &&
                this.this_row.go_flight_code
            ) {
                return true
            } else {
                return false
            }
        },

        async get_citys() {
            try {
                const response = await axios.get(`${domain_url}/backend/get_citys`);
                this.citys = response.data;
            } catch (error) {
                console.error("Error :", error);
            }
        },

        async get_max_id() {
            try {
                const response = await axios.get(`${domain_url}/backend/get_max_id/?table_name=Flight_bookings`);
                this.max_id = response.data.data.id__max;
            } catch (error) {
                console.error("Error fetching max ID:", error);
            }
        },

        // context menu ***************************

        showContextMenu(event) {
            event.preventDefault();
            this.isContextMenuActive = true;
            this.menuStyle.top = `${event.clientY}px`;
            this.menuStyle.left = `${event.clientX}px`;
            this.menuStyle.display = 'block';
            // Add a click listener to the window object to hide the context menu
            window.addEventListener('click', this.hideContextMenu);
        },

        hideContextMenu() {
            this.isContextMenuActive = false;
            this.menuStyle.display = 'none';
            // Remove the click listener from the window object
            window.removeEventListener('click', this.hideContextMenu);
        },

        // end context menu ***************************

    },

}

</script>
